import { useNavigate } from "react-router-dom"
import { Link } from "react-router-dom"
import { BigdataIcon } from "@rp/react/common/assets"
import { Box, IconButton, SxProps, Typography } from "@rp/react/web/mui/core"
import { ArrowBack, Edit } from "@rp/react/web/mui/icons"
import { HOME_ROUTE } from "@rp/react/web/router"
import { ShareIcon } from "../ShareIcon"
import { BaseMobileHeader, BaseMobileHeaderProps } from "./BaseMobileHeader"

type DVProps = {
  leftContent?: BaseMobileHeaderProps["leftContent"]
  rightContent?: BaseMobileHeaderProps["rightContent"]
  showBackArrow?: boolean
  name?: string
  isSaved?: boolean
  editable?: boolean
  hideLogo?: boolean
  onEditClick?: () => void
  isPublic?: boolean
  sx?: SxProps
}

const MobileHeader = ({
  leftContent,
  rightContent,
  name,
  showBackArrow = false,
  isPublic,
  editable = false,
  onEditClick,
  hideLogo,
  sx,
}: DVProps) => {
  const navigate = useNavigate()
  return (
    <BaseMobileHeader
      sx={sx}
      leftColumns={1}
      centerColumns={10}
      rightColumns={1}
      leftContent={
        <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
          {showBackArrow ? (
            <IconButton data-testid="back-arrow-button" onClick={() => navigate(-1)}>
              <ArrowBack />
            </IconButton>
          ) : (
            leftContent
          )}
        </Box>
      }
      rightContent={
        <Box sx={{ display: "flex", alignItems: "center", gap: 1, mr: 1 }}>{rightContent}</Box>
      }
      centerContent={
        <Box
          onClick={onEditClick}
          sx={{
            width: "100%",
            display: "flex",
            alignItems: "center",
            justifyContent: "left",
            gap: 1,
          }}
        >
          {!editable && !hideLogo ? (
            <Link to={HOME_ROUTE} style={{ alignItems: "center", display: "flex" }}>
              <BigdataIcon />
            </Link>
          ) : null}
          <Typography
            variant="body1"
            fontWeight={700}
            whiteSpace="nowrap"
            textOverflow="ellipsis"
            overflow="hidden"
            sx={{ cursor: "pointer" }}
          >
            {name}
          </Typography>
          {isPublic ? <ShareIcon shared /> : null}
          {editable ? <Edit fontSize="small" color="disabled" /> : null}
        </Box>
      }
    />
  )
}

export { MobileHeader }
